    //8 initial confusion
    &:hover #album-cover-initial-confusion {
        box-shadow: 0 0 8px $shadow-initial-confusion;
    }
    &:hover #dots-separator-album-8 {
        transition-delay: 300ms;
        color: $color-initial-confusion;
    }
}
