//media q for 1024px

@media (min-width: 1024px) {
    //nav top new
    //
    // hide mobile menu and nav for desktop row - 1024+
    .top-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: $shadow-black-transparent;
        backdrop-filter: blur(0.5rem);
        width: 100%;
        height: 3.5rem;
        padding: 0 2rem;
        position: fixed;
        z-index: 2;
    }

    #logo,
    #chevron-to-show-social-menu {
        display: block;
        width: 1.75rem;
        height: 1.75rem;
        font-size: 1.75rem;
        transition: ease-in-out 0.2s;
        cursor: pointer;
        &:hover {
            color: $grey-500;
        }
    }

    #dropdown-menu-icon {
        display: none;
    }

    .nav-desktop {
        display: block;
        width: 80%;
    }
    .desktop_menu-a {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: row;
        gap: 1.5rem;
        padding: 1rem 0rem;
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: 800;
        margin: 0;
    }

    .nav-a-desktop-menu {
        text-decoration: none;
        color: $grey-500;
        transition: 0.1s ease-in-out;
        &:hover {
            transform: scale(1.05);
            color: $grey-600;
        }
    }

    //toggle menu
    .toggle-menu {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30%;
        right: 0;
        padding: 1.5rem;
    }
    .toggle_menu-a {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 0;
        font-size: 1.4rem;
    }

    //hide this nav for small screens - social
    // social icons are hidden
    .toggle-column-right {
        display: none;
    }

    .top-nav-desktop {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-direction: column;
        margin: 5rem 2rem;
        right: 0;
        gap: 0.5rem;
        position: fixed;
        z-index: 1;
    }

    .nav-a-social {
        display: block; //test
        text-decoration: none;
        color: $white;
        margin: 0.2rem 0;
        cursor: pointer;
    }

    #nav-a-1,
    #nav-a-2,
    #nav-a-3,
    #nav-a-4,
    #nav-a-5,
    #nav-a-6,
    #nav-a-7,
    #nav-a-8,
    #nav-a-9 {
        font-size: 1.75rem;
        transition: 0.1s ease-in-out;
        &:hover {
            transform: scale(1.2);
            color: $grey-500;
        }
    }

    //section1 1024px

    .section-1 {
        padding: 5rem 1rem 0 1rem;
    }
    .section-1-card {
        padding: 4rem 3rem;
    }

    .name-and-logo {
        gap: 2.6rem;
        line-height: 5.4rem;
    }
    .h1-main-name {
        font-size: 2.7rem;
    }
    .img-square-section {
        width: 2.7rem;
        height: 2.7rem;
    }
    //introduction
    .dot-and-introduction {
        gap: 1.2rem;
        line-height: 2.4rem;
    }
    .p_introduction {
        font-size: 1.2rem;
    }
    #dot-introduction {
        display: flex;
        font-size: 0.8rem;
    }

    //main description

    .p_description {
        line-height: 2.8rem;
        font-size: 1.4rem;
        max-width: 46rem;
    }
    .hr-line-to-p {
        height: 0.1rem;
        margin: 2rem 0;
    }

    .h2_description {
        line-height: 3.8rem;
        font-size: 1.9rem;
    }

    //! section 2 i 4 padding 5.5 4.5 ma byc wieksze niz section 1 o 0.5rem

    // section 2 new albums with section 4 latest singles
    .section-2,
    .section-4 {
        padding: 5.5rem 4.5rem 2rem 5.5rem;
        gap: 1.5rem;
    }
    .h-section-2,
    .h-section-4 {
        font-size: 2rem;
        margin-bottom: 2.8rem;
    }

    //albums thing
    .albums-chooser,
    .singles-chooser {
        min-height: 26rem;
        gap: 2rem;
    }
    .album-cover,
    .single-cover {
        width: 10rem;
        height: auto;
        min-height: 10rem;
        min-width: 10rem;
    }
    .album,
    .single {
        width: 10rem;
        height: auto;
        min-height: 18rem;
    }
    .song-title-small {
        font-size: 1.2rem;
        margin-top: 0.9rem;
    }

    .content-small {
        gap: 0.6rem;
    }

    .date-small {
