    }

    .dots-separator-album,
    .dots-separator-single {
        font-size: 1.8rem;
    }

    //section3 music app chooser

    .section3 {
        padding: 0 4.5rem;
    }
    .h2-section3 {
        font-size: 2rem;
        line-height: 4rem;
    }
    .dot-and-introduction-chooser {
        gap: 0.8rem;
        line-height: 2.4rem;
        margin: 0.5rem 0 3rem 0;
    }
    .p_introduction-chooser {
        font-size: 1.2rem;
    }
    #dot-introduction-chooser {
        font-size: 1rem;
    }
    #box-1,
    #box-2,
    #box-3,
    #box-4,
    #box-5 {
        font-size: 2.4rem;
    }

    .hr-line-to-sections-chooser {
        height: 0.1rem;
        margin-top: 3rem;
    }

    //!section 5 upcoming album 1024

    .section5 {
        padding: 4rem 4.5rem;
    }
    .upcoming-album-title {
        gap: 0.9rem;
    }

    .h-section5 {
        font-size: 2rem;
        line-height: 4rem;
        margin-bottom: 1.2rem;
    }

    .title-upcoming-album {
        font-size: 1.6rem;
    }

    .p_section5-mini-description {
        font-size: 1.2rem;
    }
    .p_section5 {
        display: none;
        font-size: 1.4rem;
        line-height: 2.8rem;
        max-width: 46rem;
    }
    .p_section5-hidden {
        display: block;
        font-size: 1.4rem;
        line-height: 2.8rem;
        /* max-width: 46rem; */
        max-width: 50vw;
    }
    //img
    .upcoming-album-img-div {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 3rem 0 0 0;
    }

    .upcoming-album-img {
        width: 100%;
        height: auto;
    }
    .inner-div-upcoming-album {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 2rem;
        max-width: 31rem;
    }

    //btn 1
    .btn-discover-album {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: auto;
        background-color: transparent;
        padding-left: 2rem;
    }

    .discover-album-div {
        margin-top: 3rem;
        gap: 3rem;
    }

    .a-btn-discover-album {
        padding: 1rem;
        width: 22rem;
        height: auto;
        font-size: 1.4rem;
    }
    //section6

    .section6 {
        padding: 2rem 1rem;
    }
    .div-section6-opacity-bg {
        padding: 4rem 3rem 2rem 3rem;
    }

    .h-section6 {
        font-size: 2rem;
        line-height: 4rem;
    }
    .strimz-shop-only-icon {
        font-size: 1.2rem;
    }

    //text
    .p_section6-mini-description {
        font-size: 1.2rem;
        line-height: 2.4rem;
    }

    .p_section6-top {
        font-size: 1.4rem;
        line-height: 2.6rem;
        max-width: 100%;
    }
    .p_section6 {
        font-size: 1.4rem;
        line-height: 2.8rem;
        max-width: 46rem;
    }

    .div-dream-chasers {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 3rem 0;
        margin: 0;
    }

    .dream-chasers-img {
        width: 15rem;
    }
    .inner-div-dream-chasers {
        gap: 2rem;
    }

    // div under img

    .shop-dream-chasers {
        margin-top: 1.5rem;
    }

    .dream-shop-icon {
        font-size: 1.3rem;
    }
    .dream-chasers-fig {
        font-size: 1.1rem;
    }

    //btn

    //!footer
    //footer'top

    .footer-top {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
        gap: 4rem;
        padding: 4rem;
    }
    .footer-a-1,
    .footer-a-2 {
        gap: 1rem;
    }
    .description-and-email-input {
        width: 40%;
        gap: 1rem;
    }

    .email-input-description {
        padding: 0;
    }
    //footer bottom
    .footer-p-and-nav {
        padding-top: 2rem;
    }
    .nav-a-footer-social {
        padding: 0 1rem;
    }
}

